                else if(i === 57) width = 8.5;
                else if(i === 58) width = 8.5;
                else if(i === 59) width = 40;
                else if(i === 60) width = 30;
                else if(i === 61) width = 35;
                else if(i === 62) width = 30;
                else if(i === 63) width = 11;
                else if(i === 64) width = 30;
                else if(i === 65) width = 11;
                else if(i === 66) width = 30;
                else width = 6;
                column.format.columnWidth = factorConversion * width;
              }
            }
          }

          const setHeaderRowsHeight = async function(newSheet) {
            const usedRange = newSheet.getUsedRange();
            for(let i=0; i<5; i++) {
              const column = usedRange.getRow(i);
              let height;
              if(i === 0) height = 50;
              else if(i === 1) height = 30;
              else if(i === 2) height = 30;
              else if(i === 3) height = 60;
              else if(i === 4) height = 30;
              column.format.rowHeight = height;
            }
          }

          const buildTableGrids = async function(newSheet, optimized=false) {
            if(optimized) {
              2;
            } else {
              const usedRange = newSheet.getUsedRange();
              usedRange.load("values");
              usedRange.load("rowCount");
