              const secondTableRange = newSheet.getRange(`${self.getColumnNameFromNumber(remarksColumnNumber+3)}6:${self.getColumnNameFromNumber(remarksColumnNumber+5)}${usedRange.rowCount}`);
              for(let i=0; i<tableRange.rowCount; i++) {
