                  newSheet.getRange(`C3`).formulas = [[`=IF("Airframe"=${elementInfoAddress},${self.getColumnNameFromNumber(statusSelectedColumnNumber+12)}1&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+13)}1&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+14)}1&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+16)}1,IF("Engine 1"=${elementInfoAddress},${self.getColumnNameFromNumber(statusSelectedColumnNumber+12)}2&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+13)}2&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+14)}2&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+15)}2&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+16)}2,IF("Engine 2"=${elementInfoAddress},${self.getColumnNameFromNumber(statusSelectedColumnNumber+12)}3&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+13)}3&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+14)}3&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+15)}3&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+16)}3,IF("Engine 3"=${elementInfoAddress},${self.getColumnNameFromNumber(statusSelectedColumnNumber+12)}4&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+13)}4&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+14)}4&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+15)}4&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+16)}4,IF("Engine 4"=${elementInfoAddress},${self.getColumnNameFromNumber(statusSelectedColumnNumber+12)}5&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+13)}5&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+14)}5&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+15)}5&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+16)}5,IF("APU"=${elementInfoAddress},${self.getColumnNameFromNumber(statusSelectedColumnNumber+12)}6&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+13)}6&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+14)}6&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+15)}6&"                          "&${self.getColumnNameFromNumber(statusSelectedColumnNumber+16)}6,""))))))`]];
