NOTE 1: Sampling Tasks are not applicable whilst the aircraft is managed under the Airbus CAMO but Next Due is taken into account from previous operator for the utilisation of the next operator. For Sampling tasks having 100% requirement (Threshold and/or Interval), the next due is equal to the 100% value.
OR NOTE: Tasks performed as per AIB Work Order N WD1xxxxxx Issue x.
