            const lastOutsideTableColumn = self.getColumnNameFromNumber(usedRange.columnCount - 1);

            // Color types
            const outsideTableColor = "grey";
            const modifyColor = "yellow";
            const writeColor = "palegreen";
            const notTouchColor = "black";
            const selectColor = "pink";
            const infoColor = "skyblue";

            // Outside table
            const outsideTableRange = newSheet.getRange(`$${firstOutsideTableColumn}:$${lastOutsideTableColumn}`);
            outsideTableRange.format.fill.color = outsideTableColor;

            // Ranges to modify
            const rangesToModify = [
              `${self.getColumnNameFromNumber(remarksColumnNumber + 3)}1:${self.getColumnNameFromNumber(remarksColumnNumber + 3)}4`,
              `${self.getColumnNameFromNumber(remarksColumnNumber + 12)}2:${self.getColumnNameFromNumber(remarksColumnNumber + 12)}2`,
