            ];
            rangesToModify.forEach(r => {
              const range = newSheet.getRange(r);
              range.format.fill.color = modifyColor;
            });

            // Ranges to write
            const rangesToWrite = [
              `${self.getColumnNameFromNumber(remarksColumnNumber + 8)}7:${self.getColumnNameFromNumber(remarksColumnNumber + 11)}${usedRange.rowCount}`,
              `${self.getColumnNameFromNumber(remarksColumnNumber + 13)}7:${self.getColumnNameFromNumber(remarksColumnNumber + 13)}${usedRange.rowCount}`
            ];
            rangesToWrite.forEach(r => {
              const range = newSheet.getRange(r);
              range.format.fill.color = writeColor;
            });

            // Ranges to not touch
            const rangesToNotTouch = [
              `${self.getColumnNameFromNumber(remarksColumnNumber + 4)}1:${self.getColumnNameFromNumber(remarksColumnNumber + 6)}2`,
              `$${self.getColumnNameFromNumber(remarksColumnNumber + 19)}:$${self.getColumnNameFromNumber(remarksColumnNumber + 33)}`
            ];
            rangesToNotTouch.forEach(r => {
              const range = newSheet.getRange(r);
              range.format.fill.color = notTouchColor;
            });

            // Ranges to select
            const rangesToSelect = [
              `${self.getColumnNameFromNumber(remarksColumnNumber + 8)}1:${self.getColumnNameFromNumber(remarksColumnNumber + 8)}1`,
              `${self.getColumnNameFromNumber(remarksColumnNumber + 7)}7:${self.getColumnNameFromNumber(remarksColumnNumber + 7)}${usedRange.rowCount}`
            ];
            rangesToSelect.forEach(r => {
              const range = newSheet.getRange(r);
              range.format.fill.color = selectColor;
            });

            // Ranges to inform
            const rangesToInform = [
              `${self.getColumnNameFromNumber(remarksColumnNumber + 12)}7:${self.getColumnNameFromNumber(remarksColumnNumber + 12)}${usedRange.rowCount}`
            ];
            rangesToInform.forEach(r => {
              const range = newSheet.getRange(r);
              range.format.fill.color = infoColor;
            });
          }


          self.popupOpened = !self.popupOpened;
          try {
            const newSheet = await createNewSheet();
            if(newSheet !== false) {
              await addRemainingHeaderRows(newSheet);
              await stylingNewTableHeaderRow(newSheet);
              await changeFrozenHeaderRows(newSheet);
              await insertRemainingColumns(newSheet, this.sampleThresholdColumn, optimized);
              await insertHeaderNames(newSheet, optimized);
              await addValuesThresholdIntervalColumns(newSheet);
              await setUpRightSideParameters(newSheet);
              await addFormulasLastDoneNextDues(newSheet, optimized);
              await activateAutoFilter(newSheet);
              await setColumnsWidth(newSheet, optimized);
              await setHeaderRowsHeight(newSheet);
              await styleMergingCells(newSheet);
              await setBackgroundColor(newSheet);
              await buildTableGrids(newSheet, optimized);
              await setPrintConfigurations(newSheet);
            } else {
              console.log("The excel sheet already exists!");
            }
          } catch(e) {
            console.log(e);
            // Error message
          }
          self.popupOpened = !self.popupOpened;
        });
